import React, { useState } from 'react';
import ButtonText from '../../button-text';
import Col from '../../col';
import Grid from '../../grid';
import Link from '../../link';
import Row from '../../row';
import Tooltip from '../../tooltip';
import Typography from '../../typography';

const TooltipExample = () => {
  const [showTooltip, setShowTooltip] = useState(false);

  return (
    <Grid>
      <Row>
        <Typography tag="h2">Raw state, mouse-only interactions</Typography>
      </Row>
      <Row>
        <Col textAlign="center">
          <Tooltip
            tooltip="Because it's for demo purposes only"
            placement="right"
            trigger="hover"
            id="tooltip-12"
          >
            <ButtonText>Right</ButtonText>
          </Tooltip>
        </Col>
      </Row>
      <Row>
        <Col textAlign="center">
          <Tooltip
            tooltip="Because it's for demo purposes only"
            placement="top"
            trigger="hover"
            id="tooltip-1"
          >
            <ButtonText>Top</ButtonText>
          </Tooltip>
        </Col>
      </Row>
      <Row>
        <Col textAlign="center">
          <Tooltip
            tooltip="Because it's for demo purposes only"
            placement="bottom"
            trigger="hover"
            id="tooltip-15"
          >
            <ButtonText>Bottom</ButtonText>
          </Tooltip>
        </Col>
      </Row>
      <Row>
        <Col textAlign="center">
          <Tooltip
            tooltip="Because it's for demo purposes only"
            placement="left"
            trigger="hover"
            id="tooltip-13"
          >
            <ButtonText colorScheme="primary">Left</ButtonText>
          </Tooltip>
        </Col>
      </Row>
      <Row>
        <Col textAlign="center">
          <Typography>{"This is an example of a "}
            <Tooltip
              trigger="hover"
              id="tooltip-20"
              tooltip="Because it's for demo purposes only"
            >
              <Link href="#">link</Link>
            </Tooltip> with a tooltip.
          </Typography>
        </Col>
      </Row>
      <Row>
        <Typography tag="h2">A11y/VGAR usage, with mouse + keyboard/focus interactions</Typography>
        <Typography tag="h4">
          Use the Tooltip component as a stateless wrapper component
          to take control of the visibility of the tooltip. See code example for how to proceed...
        </Typography>
      </Row>
      <Row>
        <Col textAlign="center">
          <Tooltip
            tooltip="Because it's for demo purposes only"
            tooltipShown={showTooltip}
            placement="right"
            trigger="none"
            id="tooltip-012"
          >
            <ButtonText
              onFocus={() => setShowTooltip(true)}
              onBlur={() => setShowTooltip(false)}
              onMouseEnter={() => setShowTooltip(true)}
              onMouseLeave={() => setShowTooltip(false)}
            >
              Supports keyboard(focus) and mouse(hover)
            </ButtonText>
          </Tooltip>
        </Col>
      </Row>
    </Grid>
  );
};

export default TooltipExample;
